<template>
  <div>
    <!-- 评论列表 -->
    <div class="cmt-list">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多评论了，请发表评论" @load="onLoad" :immediate-check="false">
        <!-- 评论的 Item 项 -->
        <div class="cmt-item" v-for="item in commentArr" :key="item.com_id">
          <!-- 头部区域 -->
          <div class="cmt-header">
            <!-- 头部左侧 -->
            <div class="cmt-header-left">
              <img :src="item.aut_photo" class="avatar" />
              <span class="uname">{{ item.aut_name }}</span>
            </div>
            <!-- 头部右侧 -->
            <div class="cmt-header-right">
              <van-icon name="like" size="16" color="red" v-if="item.is_liking" @click="islove(item, false)" />
              <van-icon name="like-o" size="16" color="gray" v-else @click="islove(item, true)" />
            </div>
          </div>
          <!-- 主体区域 -->
          <div class="cmt-body" v-html="item.content"></div>
          <!-- 尾部区域 -->
          <div class="cmt-footer">{{ item.pubdate | relativeTime }}</div>
        </div>
      </van-list>
    </div>
    <!-- 底部添加评论区域 - 1 -->
    <div class="add-cmt-box van-hairline--top" v-if="!isShowCmtIpt">
      <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
      <div class="ipt-cmt-div" @click="toggleShowFn">发表评论</div>
      <div class="icon-box">
        <van-badge :content="total_count === 0 ? '' : total_count" max="99">
          <van-icon name="comment-o" size="0.53333334rem" @click="commentFn" />
        </van-badge>
        <van-icon name="star-o" size="0.53333334rem" />
        <van-icon name="share-o" size="0.53333334rem" />
      </div>
    </div>

    <!-- 底部添加评论区域 - 2 -->
    <div class="cmt-box van-hairline--top" v-else>
      <textarea placeholder="友善评论、理性发言、阳光心灵" v-fofo @blur="blurFn" v-model.trim="comText"></textarea>
      <van-button type="default" :disabled="comText.length === 0 ? true : false" @click="sendFn">发布</van-button>
    </div>
  </div>
</template>

<script>
import { commentsList, likeComments, disLikeComments, commentsAdd } from '@/api/article'
export default {
  name: 'CommentList',
  data() {
    return {
      offset: null, // 偏移量评论id
      commentArr: [],
      isShowCmtIpt: false,
      total_count: 0, // 评论数量
      comText: '', // 发布评论
      loading: false,
      finished: false,
      lastId: ''
    }
  },
  async created() {
    const { data: res } = await commentsList({
      id: this.$route.query.art_id // 文章id
    })
    this.commentArr = res.data.results
    this.total_count = res.data.total_count
    this.lastId = res.data.last_id // 分页
    if (res.data.results.length === 0) {
      this.finished = true
    }
    // console.log(res)
    /* console.log(this.$route.query)
    console.log(this) */
  },
  methods: {
    // 评论点赞
    async islove(val, bool) {
      val.is_liking = bool
      if (bool) {
        await likeComments(val.com_id)
      } else {
        await disLikeComments(val.com_id)
      }
    },
    // 评论
    toggleShowFn() {
      this.isShowCmtIpt = !this.isShowCmtIpt
    },
    // 评论消息点击事件
    commentFn() {
      // window.scrollTo(o,xx)
      // js代码是在html css运行后真实dom已经在，从document获取整个网页的标签
      // window.scrollTo使网页进行滚动，如果底下没内容则停止滚动
      /* const height = document.querySelector('.article-container').scrollHeight
      console.log(height)
      window.scrollTo(0, height) */
      // 动画实现
      // 既能滚动又能带动画原生方法，不选择评论因为头部导航会被挡住
      // (有的电脑不支持)
      document.querySelector('.like-box').scrollIntoView({
        behavior: 'smooth'
      })
    },
    // 发布按钮
    async sendFn() {
      // 把评论加入到列表里
      const { data: res } = await commentsAdd({
        id: this.$route.query.art_id,
        content: this.comText
      })
      // console.log(res)
      this.total_count++
      this.commentArr.unshift(res.data.new_obj)
      this.comText = ''
      this.commentFn()
    },
    // 失去焦点
    blurFn() {
      // 点击发布按钮不执行，原因是评论容器2在点击发布时输入框失去焦点，整个dom移除，点击事件未来得及执行
      setTimeout(() => {
        this.isShowCmtIpt = false
      }, 0)
    },
    // 加载更多
    async onLoad() {
      // 这个判断和immediate-check功能一样,双重保险，不然屏幕滚动就会导致触发
      if (this.commentArr.length > 0) {
        const { data: res } = await commentsList({
          id: this.$route.query.art_id,
          offset: this.lastId
        })
        // console.log(res)
        this.commentArr = [...this.commentArr, ...res.data.results]
        this.total_count = res.data.total_count // 总数量
        this.lastId = res.data.last_id
        this.loading = false
        if (res.data.last_id === null) {
          this.finished = true
        }
      } else {
        // 如果没有immediate-check，这个得让加载中变为false
        this.loading = false
      }
    }
  }
}
</script>

<style scoped lang="less">
.cmt-list {
  padding: 20px;
  background-color: #fff;
  .cmt-item {
    padding: 30px 0;
    .cmt-item {
      border-top: 1px solid #f8f8f8;
    }
    .cmt-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .cmt-header-left {
        display: flex;
        align-items: center;
        .avatar {
          width: 80px;
          height: 80px;
          background-color: #f8f8f8;
          border-radius: 50%;
          margin-right: 30px;
        }
        .uname {
          font-size: 24px;
        }
      }
    }
    .cmt-body {
      font-size: 28px;
      line-height: 56px;
      text-indent: 2em;
      margin-top: 12px;
      word-break: break-all;
    }
    .cmt-footer {
      font-size: 24px;
      color: gray;
      margin-top: 20px;
    }
  }
}
/*美化 - 文章详情 - 底部的评论页面 */
// 外层容器
.art-cmt-container-1 {
  padding-bottom: 92px;
}
.art-cmt-container-2 {
  padding-bottom: 160px;
}

// 发布评论的盒子 - 1
.add-cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 92px;
  line-height: 92px;
  padding-left: 20px;
  .ipt-cmt-div {
    flex: 1;
    border: 2px solid #efefef;
    border-radius: 30px;
    height: 60px;
    font-size: 24px;
    line-height: 60px;
    padding-left: 30px;
    margin-left: 20px;
    background-color: #f8f8f8;
  }
  .icon-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    line-height: 0;
  }
}

.child {
  width: 40px;
  height: 40px;
  background: #f2f3f5;
}

// 发布评论的盒子 - 2
.cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 160px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 24px;
  padding-left: 20px;
  box-sizing: border-box;
  background-color: white;
  textarea {
    flex: 1;
    height: 66%;
    border: 2px solid #efefef;
    background-color: #f8f8f8;
    resize: none;
    border-radius: 12px;
    padding: 10px;
  }
  .van-button {
    height: 100%;
    border: none;
  }
}
</style>
