<template>
  <div class="article-detail">
    <!-- Header 区域 -->
    <van-nav-bar fixed title="文章详情" left-arrow @click-left="$router.back()" class="page-nav-bar" :border="false" />

    <!-- 文章信息区域 -->
    <div class="article-container">
      <!-- 文章标题 -->
      <h1 class="art-title">{{ artObj.title }}</h1>

      <!-- 用户信息 -->
      <van-cell center :title="artObj.aut_name" :label="artObj.pubdate | relativeTime">
        <template #icon>
          <img :src="artObj.aut_photo" alt="" class="avatar" />
        </template>
        <template #default>
          <div>
            <van-button type="info" size="mini" v-if="artObj.is_followed" @click="followFn(false)">已关注</van-button>
            <van-button icon="plus" type="info" size="mini" plain v-else @click="followFn(true)">关注</van-button>
          </div>
        </template>
      </van-cell>

      <!-- 分割线 -->
      <van-divider></van-divider>

      <!-- 文章内容 -->
      <div class="art-content" v-html="artObj.content"></div>

      <!-- 分割线 -->
      <van-divider>End</van-divider>

      <!-- 点赞 -->
      <!-- attitude 用户对文章的态度, -1: 无态度，0-不喜欢，1-点赞 -->
      <div class="like-box">
        <van-button icon="good-job" type="danger" size="small" v-if="artObj.attitude === 1" @click="attitudeFn(0)">已点赞</van-button>
        <van-button icon="good-job-o" type="danger" plain size="small" v-else @click="attitudeFn(1)">点赞</van-button>
      </div>
    </div>
    <!-- 文章评论 -->
    <div>
      <CommentList />
    </div>
  </div>
</template>

<script>
// 文章详情 点赞或取消
import { articleDetail, likeArticle, disLikeArticle } from '@/api/article'
// 关注用户
import { followUser, unFollowUser } from '@/api/user'
import CommentList from '@/views/articleDetail/CommentList'
export default {
  name: 'ArticleDetail',
  props: {
    art_id: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      artObj: {}
    }
  },
  // 获取详情
  async created() {
    // console.log(this.art_id)
    const { data: res } = await articleDetail(this.art_id)
    // console.log(res)
    this.artObj = res.data
  },
  methods: {
    // 关注或者不关注
    async followFn(bool) {
      // if(bool===true){
      // 点击按钮关注或者不关注
      this.artObj.is_followed = bool
      // 页面进行变换
      if (bool) {
        // 掉接口关注
        await followUser(this.artObj.aut_id)
        // console.log(res)
      } else {
        await unFollowUser(this.artObj.aut_id)
        // console.log(res)
      }
      // }
    },
    // 点赞取消点赞
    async attitudeFn(num) {
      this.artObj.attitude = num
      if (num) {
        // console.log(num)
        const { data: res } = await likeArticle(this.artObj.art_id)
        console.log(res)
      } else {
        const { data: res } = await disLikeArticle(this.artObj.art_id)
        console.log(res)
      }
    }
  },
  components: { CommentList }
}
</script>

<style lang="less" scoped>
.article-detail {
  padding: 98px 0;
  .article-container {
    position: relative;
    padding: 20px;
    background-color: #fff;
    // height: 83vh;
    overflow-y: auto;
  }
  .art-title {
    font-size: 32px;
    font-weight: bold;
    margin: 20px 0;
  }

  .art-content {
    font-size: 24px;
    line-height: 48px;
    width: 100%;
    overflow-x: scroll;
    word-break: break-all;
    /deep/ img {
      width: 100%;
    }
    /deep/ pre {
      white-space: pre-wrap;
      word-wrap: break-word;
    }
  }

  .van-cell {
    padding: 10px 0;
    &::after {
      display: none;
    }
  }

  .avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-color: #f8f8f8;
    margin-right: 10px;
    border: none;
  }

  .like-box {
    display: flex;
    justify-content: center;
  }
}
</style>
